@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Annie+Use+Your+Telescope&family=Bad+Script&family=Cookie&family=Waiting+for+the+Sunrise&family=Lato&display=swap);
body {
  font-family: 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    overflow-x: hidden;
}
/*
'Amatic SC', cursive;
'Annie Use Your Telescope', cursive;
'Bad Script', cursive;
'Cookie', cursive;
'Waiting for the Sunrise', cursive;
*/

:root {
    --colour-dark: #424242;
    --colour-primary: #D96690;
    --colour-secondary: #96D2D9;
    --colour-highlight: #CFCEF2;
    --colour-green: #BAD9C8;
    --font-primary: 'Annie Use Your Telescope', cursive;
    --font-secondary: 'Annie Use Your Telescope', cursive;
    --font-tertiary: 'Amatic SC', cursive;
    --font-normal: 'Lato', sans-serif;
}
body {
    margin: 0;
    background-color: var(--colour-dark);
}
.header {
    position: relative;
    background-color: var(--colour-highlight);
    background-image: url(/static/media/background.6972363b.png);
    background-size: auto;
    background-repeat: no-repeat;
    background-position: right;
    display: block;
    padding-top: 1em;
    padding-bottom: 1em;
    height: 4em;
    width: 100vw;
}
.header img {
    float: left;
    width: 4em;
}
.header h1 {
    display: inline;
    margin-top: 0px;
    margin-left: 0px;
    padding-left: 10px;
    color: var(--colour-primary);
    font-size: 3em;
    font-family: var(--font-primary);
    font-weight: normal;
}
.header ul {
    list-style: none;
    float: right;
    margin: 0;
    border: 0;
    height: 100%;
    z-index: 1;
}
.header ul li {
    display: inline-block;
    line-height: 2em;
    padding-right: 40px;
    font-family: var(--font-secondary);
    font-size: 2em;
    vertical-align: middle;
    z-index: 10;
}

.header a {
    text-decoration: none;
    color: var(--colour-dark);
}
.header a:hover {
    color: var(--colour-primary);
}
.header a:visited {
    color: var(--colour-dark);
}
.header a:visited:hover {
    color: var(--colour-primary);
}
.section {
    margin: auto;
    text-align: center;
}
.section h1 {
    font-family: var(--font-primary);
    color: var(--colour-secondary);
    font-size: 3em;
    font-weight: normal;
    margin: 0;
}
iframe {
    width: 40vw;
    height: 40vh;
}
.modal .project-details {
    background-color: var(--colour-green );
    padding: 5px;
}

.modal .project-details h1 {
    color: var(--colour-primary);
    font-weight: normal;
    font-family: var(--font-tertiary);
    font-size: 4em;
    margin: 0;
    padding: 10px;
}
.modal .project-details pre {
    color: var(--colour-dark);
    font-family: var(--font-normal);
    font-size: 1.2em;
}
.project-board {
    margin: 0 50px 50px 50px;
    display: flex;
    flex-wrap: wrap;
}
.project-board .project {
    overflow: hidden;
    position: relative;
    min-width: 500px;
    margin: 30px;
    flex: 1 1;
    cursor: pointer;
}
.project-board .project img {
    height: 256px;
    transition: transform 0.4s, -webkit-filter 0.4s;
    transition: filter 0.4s, transform 0.4s;
    transition: filter 0.4s, transform 0.4s, -webkit-filter 0.4s;
}
.project-board .project a {
    margin: 0;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: var(--font-primary);
    font-size: 3em;
    color: white;
    transition: -webkit-filter 0.4s;
    transition: filter 0.4s;
    transition: filter 0.4s, -webkit-filter 0.4s;
    -webkit-filter: opacity(0);
            filter: opacity(0);
}
.project-board .project.hover img {
    -webkit-filter: blur(2px);
            filter: blur(2px);
    transform: scale(1.05);
}
.project-board .project.hover a {
    -webkit-filter: opacity(1);
            filter: opacity(1);
    cursor: pointer;
}
.modal-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    background-color: #00000088;
    width: 100vw;
    height: 100vh;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
.modal-wrapper .modal {
    position: relative;
    top: -10vh;
    width: 50vw;
    height: 50vh;
}
