.project-board {
    margin: 0 50px 50px 50px;
    display: flex;
    flex-wrap: wrap;
}
.project-board .project {
    overflow: hidden;
    position: relative;
    min-width: 500px;
    margin: 30px;
    flex: 1;
    cursor: pointer;
}
.project-board .project img {
    height: 256px;
    transition: filter 0.4s, transform 0.4s;
}
.project-board .project a {
    margin: 0;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: var(--font-primary);
    font-size: 3em;
    color: white;
    transition: filter 0.4s;
    filter: opacity(0);
}
.project-board .project.hover img {
    filter: blur(2px);
    transform: scale(1.05);
}
.project-board .project.hover a {
    filter: opacity(1);
    cursor: pointer;
}