.modal-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    background-color: #00000088;
    width: 100vw;
    height: 100vh;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
.modal-wrapper .modal {
    position: relative;
    top: -10vh;
    width: 50vw;
    height: 50vh;
}