.header {
    position: relative;
    background-color: var(--colour-highlight);
    background-image: url("../../background.png");
    background-size: auto;
    background-repeat: no-repeat;
    background-position: right;
    display: block;
    padding-top: 1em;
    padding-bottom: 1em;
    height: 4em;
    width: 100vw;
}
.header img {
    float: left;
    width: 4em;
}
.header h1 {
    display: inline;
    margin-top: 0px;
    margin-left: 0px;
    padding-left: 10px;
    color: var(--colour-primary);
    font-size: 3em;
    font-family: var(--font-primary);
    font-weight: normal;
}
.header ul {
    list-style: none;
    float: right;
    margin: 0;
    border: 0;
    height: 100%;
    z-index: 1;
}
.header ul li {
    display: inline-block;
    line-height: 2em;
    padding-right: 40px;
    font-family: var(--font-secondary);
    font-size: 2em;
    vertical-align: middle;
    z-index: 10;
}

.header a {
    text-decoration: none;
    color: var(--colour-dark);
}
.header a:hover {
    color: var(--colour-primary);
}
.header a:visited {
    color: var(--colour-dark);
}
.header a:visited:hover {
    color: var(--colour-primary);
}